<template>
  <div id="app" v-cloak>
    <div id="start"  
      @click = "start = true"
      v-show = "start == false">
      <h1>QUIZ</h1>
      <hr>
      <button id="start-btn"
      >Start</button>
    </div>

    <div id="content" v-show = "start == true">
      <div>
        <div id="time">
          <h2>{{count.time}}</h2>
        </div>
      <div id="question">
        {{ questions[currentQuestion].name }}:
        {{ questions[currentQuestion].content }}
        
      </div>

      <div id="mutilAnswer" >
        <div class="answer"
         v-for="(answer, index) in questions[currentQuestion].answers" :key="index"
        @click = "pickAnswer(currentQuestion, index, count)"
        :class = "{picked: questions[currentQuestion].picked == index, showCorrectAnswer: showCRA.id == index}"        
        >
          {{ answer }}
        </div>
      </div>
    </div>

    <!-- <div id="result" v-show = "showResult">
      Correct answers: {{ questions.filter(question => question.picked == question.correctAnswer).length }}
    </div> -->
    
    <div id="btn">
      <!-- <button id="prevbtn" @click ="currentQuestion--"
      v-show = "currentQuestion > 0 && !showResult"
      >prev</button> -->

      <button id="nextbtn" @click ="nextQuestion()"
      v-show = "currentQuestion < questions.length - 1"
      :disabled = "disNextBtn(currentQuestion)">Next</button>

      <!-- <button id="resultbtn"
      @click = "showResult = true"
      v-show = "currentQuestion == questions.length - 1"
      :disabled = "questions[currentQuestion].picked == -1">
        Result
      </button>  -->
    </div>
    </div>
    <div id="restart-btn"
      v-show = "isGameOver">
        <h2>GAME OVER</h2>
        <button @click = "currentQuestion = 0; showCRA.id = -1; questions.forEach(question => question.picked = -1); isGameOver = false;"> ok</button>
      </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      start: false,
      currentQuestion: 0,
      showCRA: {id: -1},
      count: {time: 3, IsTimeOut: false},
      isGameOver: false,
      questions: [
        {
          name: "Câu 1",
          content: "Haiku là thể thơ truyền thống của nước nào?",
          answers: ["A: Nhật Bản","B: Mông Cổ","C: Trung Quốc","D: Hàn Quốc"],
          correctAnswer: 0,
          picked: -1   
        },

        {
          name: "Câu 2",
          content: "Nhà văn Kim Dung (Trung Quốc) nổi tiếng với thể loại văn học gì?",
          answers: ["A: Truyện trinh thám","B: Tiểu thuyết kiếm hiệp","C: Sử thi","D: Thơ lãng mạn"],
          correctAnswer: 1,
          picked: -1   
        },

        {
          name: "Cau 3",
          content: 'Bộ phim "Chị Dậu" được chuyển thể từ tác phẩm văn học nào?',
          answers: ["A: Người mẹ cầm súng","B: Vợ chồng A Phủ","C: Tuổi thơ dữ dội","D: Tắt đèn"],
          correctAnswer: 3,
          picked: -1   
        },

        {
          name: "Câu 4",
          content: 'Nhạc sĩ nào là người phổ nhạc ca khúc "Cây đàn sinh viên"?',
          answers: ["A: Bảo Chấn","B: Trịnh Công Sơn","C: Quốc An","D: Trần Tiến"],
          correctAnswer: 2,
          picked: -1   
        }

        ,

        {
          name: "Câu 5",
          content: 'Việt Nam có chung biên giới với quốc gia nào dưới đây?',
          answers: ["A: Thái Lan","B: Myanmar","C: Trung Quốc","D: Bruney"],
          correctAnswer: 2,
          picked: -1   
        }

        
      ]
    }
  },
  methods: {
    disNextBtn(currentQuestion) {
      if (this.isGameOver == false) {
        if (this.questions[currentQuestion].picked == this.questions[currentQuestion].correctAnswer && this.showCRA.id != -1) {
          return false;
        }else return true;       
      }else return true;
    },
    nextQuestion() {
      this.currentQuestion++;
      this.showCRA.id = -1;
    },
    showCorrectAnswer(cra) {
      this.showCRA.id = cra;
    },
    checkAns(currentQuestion) {
      if(this.questions[currentQuestion].picked != this.questions[currentQuestion].correctAnswer) {
        this.isGameOver = true;
      }
    },
    pickAnswer (currentQuestion, index, count) {
      if(this.questions[currentQuestion].picked == index){
        this.questions[currentQuestion].picked = -1;

      }
      else {
        this.questions[currentQuestion].picked = index;
        //reset thời gian
        count.time = 3;
        count.IsTimeOut = false;
        //Đếm ngược thời gian
        console.log(count.time);
        var countTime = setInterval( () => {
          count.time--;
          if(count.time < 1) {
            count.IsTimeOut = true;
            clearInterval(countTime);
            this.showCorrectAnswer(this.questions[currentQuestion].correctAnswer);
            this.checkAns(currentQuestion);
          }
          console.log(count.time);
          console.log(count.IsTimeOut);
        },1000); 
      }
    }
  }
}

</script>

